import React, { PureComponent } from 'react'
import {Form, Input, Button} from 'antd'
import { LoadingOutlined } from '@ant-design/icons'

export default class AddUserPane extends PureComponent{
  formRef = React.createRef()
  state = {
    loading: false,
  }

  componentDidMount() {
    if (this.props.userkey) {
      this.formRef.current.setFieldsValue(this.props.userkey);
    }
  }

  render() {
    return (
      <>
        <Form ref={this.formRef} onFinish={this.props.onFinish} initialValues={{
          userID: "00000000-0000-0000-0000-000000000000",
          userAccount: "",
          userPassword: "",
          userName: "",
          phone: ""
        }}>
          <Form.Item name="userID" label="" hidden={true}>
            <Input placeholder="" autoComplete="off"/>
          </Form.Item>
          <Form.Item name="userAccount" label="" rules={[{
            required: true,
            message: ""
          }]}>
            <Input placeholder="" autoComplete="off"/>
          </Form.Item>
          {
            !this.props.userkey ? (
              <Form.Item name="userPassword" label="" rules={[{
                required: true,
                message: ""
              }]}>
                <Input.Password placeholder="" autoComplete="off"/>
              </Form.Item>
            ):<></>
          }
          <Form.Item name="userName" label="" rules={[{
            required: true,
            message: ""
          }]}>
            <Input placeholder="" autoComplete="off"/>
          </Form.Item>
          <Form.Item name="phone" label="" rules={[
            //   {
            //     required: true,
            //     message: ""
            // },
            {
                pattern: /^1(3|4|5|6|7|8|9)\d{9}$/,
                message: "11"
              }]}>
            <Input placeholder="" autoComplete="off" />
          </Form.Item>
          <Form.Item className="form-footer">
            <Button type="default" className="login-form-button" onClick={this.props.onCancel}></Button>
            <Button type="primary" htmlType="submit" className="login-form-button" ref="submit" disabled={this.state.loading ? true : false}>
              {
                this.state.loading ? <LoadingOutlined /> : <></>
              }
              
            </Button>
          </Form.Item>
        </Form>
      </>
    )
  }
}