import React, { PureComponent } from 'react'
import { Modal, message } from 'antd'

import { GetUserList, AddUser, UpdateUser, DeleteUser,UpdateUserPassword } from 'api/user'

import DataTable from 'components/common/Table'
import Search from './components/Search'
import ModifyPw from './components/ModifyPw'
import AddUserPane from './components/AddUser'
import img404 from 'assets/img/common/404.png'

import {getCookie} from 'utils'

class UserList extends PureComponent {
  state = {
    dataSource: [],
    count: 0,
    isShow: false,
    modalContent: "",
    selectedRowKeys: []
  }

  componentDidMount() {
    this.GetUserList("");
  }
  //
  GetUserList = (res) => {
    GetUserList(res).then(res => {
      if (res.code === 0) {
        let dataList = [];
        res.data.forEach(el => {
          el.key = el.userID;
          dataList.push(el)
        })

        this.setState({
          dataSource: dataList,
          count: res.data.length
        })
      } else {
        message.error(""+ res.msg)
      }
    })
  }
  
  //
  search = (res) => {
    console.log(res)
    this.GetUserList(res)
  }
  addUserForm = (res) => {
    AddUser(res).then(res => {
      console.log(res)
      if (res.code === 0) {
        message.info("")
        this.GetUserList("");
        this.onCancel();
      } else {
        message.error(""+ res.msg)
      }
    })
  }
  modifyUserForm = res => {
    UpdateUser(res).then(res => {
      if (res.code === 0) {
        message.info("")
        this.GetUserList("");
        this.onCancel();
      } else {
        message.error(""+ res.msg)
      }
    })
  }
  //
  addUser = () => {
    this.setState({
      isShow: true,
      modalContent: <AddUserPane key={ Date() } onCancel={this.onCancel} onFinish={ this.addUserForm }/>
    })
  }
  //
  modifyUser = (obj) => {
    this.setState({
      isShow: true,
      modalContent: <AddUserPane key={ Date() } userkey={ obj } onCancel={this.onCancel} onFinish={ this.modifyUserForm }/>
    })
  }
  //
  delUser = () => {
    if (this.state.selectedRowKeys.length > 0) {
      DeleteUser(this.state.selectedRowKeys).then(res => {
        if (res.code === 0) {
          message.info("")
          this.GetUserList("");
        } else {
          message.error(""+ res.msg)
        }
      })
    } else {
      message.error("")
    }
  }
  //
  ModifyPwForm = (key, res) => {
    UpdateUserPassword({
      userID: key,
      userPassword: res.userPassword
    }).then(res => {
      if (res.code === 0) {
        message.info(res.msg)
        this.onCancel();
      } else {
        message.error(res.msg)
      }
    })
  }
  //
  modifyPw = (key) => {
    this.setState({
      isShow: true,
      modalContent: <ModifyPw userkey={key} onCancel={this.onCancel} onFinish={ (res)=>{this.ModifyPwForm(key,res)} }/>
    })
  }

  onCancel = () => {
    this.setState({isShow: false})
  }

  onSelectChange = selectedRowKeys => {
    this.setState({ selectedRowKeys });
  };
  
  render() {

    return (
      <div className="tableList" style={{'paddingLeft': '25px'}}>
        {
          getCookie("userName") === "SuperAdmin" ? (
            <>
              <Search searchForm={this.search} addUser={this.addUser} delUser={ this.delUser }/>
              <div className="tableContain">
                <DataTable
                  dataSource={this.state.dataSource}
                  rowSelection={{
                    columnWidth: "50px",
                    selectedRowKeys: this.state.selectedRowKeys,
                    onChange: this.onSelectChange,
                  }}
                  loadMore={this.loadMore}
                  count={this.state.count}
                  columns={[
                  {
                    title: '',
                    dataIndex: 'key',
                    width: "70px",
                    render: (key, i) => {
                      return <span className="serialNum">{this.state.dataSource.indexOf(i) + 1}</span>
                    }
                  },
                  {
                    title: '',
                    dataIndex: 'userAccount',
                  },
                  {
                    title: '',
                    dataIndex: 'userName',
                  },
                  {
                    title: '',
                    dataIndex: 'phone',
                    width: '200px',
                  },
                  {
                    title: '',
                    dataIndex: 'userID',
                    render: (key, obj) => {
                      if (obj.userAccount === "SuperAdmin") {
                        return <></>
                      } else {
                        return (
                          <>
                            <button className="ant-opt-btn" onClick={() => {
                            this.modifyUser(obj)
                          }}></button>
                            {
                              getCookie("userName") === "SuperAdmin" ? (
                                <button className="ant-opt-btn ant-opt-btn-normal"
                                  onClick={() => { this.modifyPw(key)} } style={{ "marginLeft": "20px" }}></button>
                              ): (
                                  <></>
                              )
                            }
                          </>
                        )
                      }
                    }
                  }
                ]} />
                
                <Modal
                  title=""
                  visible={this.state.isShow}
                  onCancel={this.onCancel}
                  footer={null}
                  width="460px"
                >
                {
                  this.state.modalContent
                }
                </Modal>
              </div>
            </>
          ) :
          <div className="error-page">
            <img src={ img404 } alt="404"/>
            <div className="error-msg"></div>   
          </div>
        }
      </div>
    )
  }
}

export default UserList